import React, { Component } from 'react'
import Note from "./Note"
import Header from "./Header"

class NoteDisplay extends Component {
  constructor() {
    super()
    this.state = {
      notes: []
    }
  }

  componentDidMount() {
    const json = localStorage.getItem('notes')
    const notes = JSON.parse(json)
    this.setState(() => ({ notes }))
  }

  deleteNote = (e) => {
    const noteId = parseInt(e.target.id)
    const newArr = this.state.notes;
    const newNotes = newArr.filter(note => {
      return note.id !== noteId
    })
    this.setState({
      notes: newNotes
    })
  }

  addNote = (newNote) => {
    const array = this.state.notes || [];
    const newArray = array.push(newNote);
    this.setState({
      note: newArray
    })
  }

  componentDidUpdate(prevProps, prevStates){
    const json = JSON.stringify(this.state.notes)
    localStorage.setItem('notes', json)
  }

  render() {
    
    return (
      <section>
      <Header addNote={this.addNote}/>
      {this.state.notes ?
        <div>
        <section className="note-display">
          {this.state.notes.map(note => <Note key={note.id} id = {note.id} title={note.title} info={note.info} deleteNote={this.deleteNote}/>)}
        </section>
        </div> :
        <h1 className="add-note-title">add a note!</h1>
        }
      </section>
    );
  }
}

export default NoteDisplay;